<template>
  <div style="background-color:#f6f6f6;position:fixed;top:0;bottom:0;left:0;right:0;">
    <mt-header :title="header">
      <mt-button
        v-if="menuShow"
        slot="left"
      >
        <div class="flex">
          <img
            src="@/assets/five/title.png"
            class="flex_img"
          >
          <div
            style="display:flex;align-items:center;"
            @click="show_manager = true"
          >
            <div v-if="!rolesAll">
              <div>{{ c_role_photo_info.phone }}</div>
              <div>{{ c_role_photo_info && c_role_photo_info.id == '1' ? '管理员' : c_role_info ? c_role_info.roleName : '' }}</div>
            </div>
            <div v-else>
              <div>{{ c_role_photo_info.phone }}</div>
              <div>{{ rolesAll[0].roleName }}</div>
            </div>
            <van-icon name="arrow" />
          </div>
        </div>
      </mt-button>
      <mt-button
        v-else-if="(newVal == '/quickLiveOnline' && oldVal == '/quicklive') || (newVal == '/quickLiveOnline' && oldVal == '/paySet-page')"
        slot="left"
        icon="back"
        @click.native="backIndex"
      >首页</mt-button>
      <mt-button
        v-else
        slot="left"
        icon="back"
        @click="backReturn"
      >返回</mt-button>
      <mt-button
        v-if="menuShow"
        slot="right"
        @click.native="logout"
      >重新登录</mt-button>
      <mt-button
        v-else-if="header == '订单详情' || header == '申请详情' "
        @click.native="btnMore(header)"
        slot="right"
      >操作记录</mt-button>
      <mt-button
        v-else
        slot="right"
        @click.native="logout"
      >重新登录</mt-button>
    </mt-header>
    <!-- 账号管理 -->
    <van-popup
      v-model='show_manager'
      position="bottom"
      class="manage_bottom"
    >
      <div
        v-if="c_role_photo_info && c_role_photo_info.id != '1'"
        @click="change('切换角色')"
        style="border-bottom:1px solid #EFEFF4;"
      >切换角色</div>
      <div @click="change('账号管理')">账号管理</div>
      <div style="height:4px;background:#EFEFF4;"></div>
      <div @click="show_manager = false">取消</div>
    </van-popup>
    <!-- 内容区间 -->
    <router-view></router-view>

  </div>
</template>
<script>
import { mapState } from 'vuex';
import SubMenueView from './components/sub-menue-view/sub-menue-view.vue';
export default {
  components: { SubMenueView },
  data() {
    return {
      c_role_photo_info: null,
      menuVisible: false,
      isBusiness: false,
      show_manager: false,
      c_role_info: null,
      newVal: '',
      oldVal: ''
    };
  },
  computed: {
    ...mapState(['menuShow', 'header', 'rolesAll'])
  },
  created() {
    try {
      this.c_role_photo_info = JSON.parse(localStorage.getItem('current_user'));
      if (this.c_role_photo_info && this.c_role_photo_info.id == '1') {
      } else {
        let c_roles = JSON.parse(localStorage.getItem('roles'));
        this.c_role_info = c_roles[0];
      }
    } catch (error) {
      console.error("[src/components/Home/index.vue created -> 105] error: ", error);
    }
  },
  methods: {
    change(title) {
      this.$router.push({
        path: '/manager',
        query: {
          title: title
        }
      });
      this.show_manager = false;
    },
    backIndex() {
      this.$router.replace('/dashboard');
    },
    backReturn() {
      let isSdffInfo = JSON.parse(localStorage.getItem('isSdff'));
      if (this.newVal == '/order' && isSdffInfo && isSdffInfo.yesOrNo == 'yes') {
        this.$router.push('/dashboard');
      } else {
        this.$router.back(-1);
      }
    },
    // 重新登录
    logout() {
      window.location.href = '/login';
    },
    // 操作更多
    btnMore(title) {
      this.$router.push({
        path: '/more',
        query: {
          type: title == '订单详情' ? 'order' : 'apply'
        }
      });
    }
  },
  watch: {
    '$route.path': {
      handler(newVal, oldVal) {
        this.newVal = newVal;
        this.oldVal = oldVal;
        if (newVal == '/hotel-manager-page' || newVal == '/dashboard') {
          this.$store.commit('setHeader', '');
        }
        if (newVal == '/order') {
          this.$store.commit('setHeader', '订单管理');
        }
        if (newVal == '/finance') {
          this.$store.commit('setHeader', '财务管理');
        }
      },
      immediate: true
    }
  }
};
</script>
<style scoped>
.flex {
  display: flex;
  text-align: left;
  align-items: center;
}
.flex_img {
  height: 16px;
  padding-right: 8px;
}
.manage_bottom {
  width: 100%;
  line-height: 40px;
  font-size: 16px;
  color: #000;
}
::v-deep .mint-header {
  color: #fff;
  height: 50px;
  background-color: #000;
}
</style>



